import { Component, OnInit } from '@angular/core';
import { SubscriptionService } from '../../services/subscription.service';
import { NgbModal,NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { Observable } from "rxjs/Rx"
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: 'app-subscription-view',
  templateUrl: './subscription-view.component.html',
  styleUrls: ['./subscription-view.component.scss']
})
export class SubscriptionViewComponent implements OnInit {

  constructor(
    private modalService: NgbModal,
    private service: SubscriptionService,
    private toastr:ToastrService,
    private spinner:NgxSpinnerService,
  ) { }

  subsciption:any;
  modal : NgbModalRef ;

  invoices:Observable<any>;

  ngOnInit(): void {
    this.getActiveSubsciption();
    this.getInvoices();
  }

  getActiveSubsciption(){
    this.spinner.show();
    this.service.getActiveSubsciption().subscribe((response:any) => {
      this.subsciption = response;
      this.spinner.hide();
      this.subsciption.percent = (response.ContingentInUse / response.Contingent) * 100;
    },err => {
        //handle errors here
        this.toastr.error(err.error.Message);
    });
  }

  getInvoices(){
    this.spinner.show();
    this.service.getInvoices().subscribe((response:any) => {
      this.spinner.hide();
      this.invoices = response.Objects;
    },err => {
        //handle errors here
        this.toastr.error(err.error.Message);
    });
  }
  openSmallModal( smallModalContent ) {
    this.modal = this.modalService.open( smallModalContent , { size : 'sm',centered : true } );
  }
  cancelSubscription(){
    console.log('Cancel Subscription');
  }

  downloadInvoice(invoice){
    this.spinner.show();
    this.service.downloadInvoice(invoice.Id).subscribe((response:any) => {
      var blob = new Blob([response], {type: 'application/pdf'});
      var downloadURL = window.URL.createObjectURL(response);
      var link = document.createElement('a');
      link.href = downloadURL;
      link.download = invoice.InvoiceNumber+".pdf";
      link.click();
      this.spinner.hide();
    },err => {
        //handle errors here
        this.spinner.hide();
        this.toastr.error(err.error.Message);
    });
  }

}
