import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VerifySubscriptionComponent } from './components/verify-subscription/verify-subscription.component';
import { SubscriptionViewComponent } from './components/subscription-view/subscription-view.component';
import { NewsubscriptionComponent } from './components/subscription-new/subscription-new.component';

const routes: Routes = [
  {
    path: '',
    component: SubscriptionViewComponent,
   },
   {
      path: 'add',
      component: NewsubscriptionComponent,
    },
    {
      path: 'VerifySubscription',
      component: VerifySubscriptionComponent,
    },
 
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class SubscriptionRoutingModule { }
